/**
 * @fileoverview H5CrossMarketing - tpl.
 * @author <junmu.cjw@alibaba-inc.com>.
 */

export const mods = {
    /**  start **/
    crossMarketing_card_2:'{@if items && items.length > 0}'+
                                '<div class="cmod-entrance" data-spm="cmv2c2">'+
                                    '{@if title}'+
                                        '<div class="cmod-entrance-title">${title}</div>'+
                                    '{@/if}'+
                                    '{@each items as item, index}'+
                                    '<div class="cmod-entrance-item J_crossMarketingLink cmod-module-item"  data-cross-market data-resource-holder="${resourceHolderName}" data-template-id="${templateId}" data-action="${item.actionParam}" data-link="${item.link}" data-trackargs="${item.trackArgs|serialize}" data-spm-click="gostr=/tbtrip;locaid=d0${index}">'+
                                        '<div class="cmod-entrance-left">'+
                                            '<img class="J_crossMarketingImage" data-backup="small" src="${item.picUrl | removeProtocol}"/>'+
                                            '<div class="cmod-entrance-desc">'+
                                                '<div class="cmod-entrance-item-title">${item.title}</div>'+
                                                '<div class="cmod-entrance-item-subtitle">'+
                                                    '${item.subTitle}'+
                                                '</div>'+
                                            '</div>'+
                                        '</div>'+
                                        '<div class="cmod-entrance-benefit">${item.text}</div>'+
                                    '</div>'+
                                    '{@/each}'+
                                '</div>'+
                            '{@/if}',

    crossMarketing_item_1:'{@if items && items.length > 0 }'+
                                '<div class="cmod-goods cmod-module" data-spm="cmv2i1">'+
                                    '{@if title}'+
                                    '<div class="cmod-goods-title">${title}</div>'+
                                    '{@/if}'+
                                    '<div class="cmod-goods-items">'+
                                    '{@each items as item, index}'+
                                        '<div class="cmod-goods-item J_crossMarketingLink cmod-module-item" data-cross-market data-resource-holder="${resourceHolderName}" data-template-id="${templateId}" data-action="${item.actionParam}" data-link="${item.link}" data-trackargs="${item.trackArgs|serialize}" data-spm-click="gostr=/tbtrip;locaid=d0${index}" >'+
                                            '<div class="cmod-goods-item-content">'+
                                                '<img class="J_crossMarketingImage" data-backup="middle" lazysrc="${item.picUrl | removeProtocol}" src="//img.alicdn.com/tfs/TB1kCiMQpXXXXXMXXXXXXXXXXXX-226-226.png"/>'+
                                                '<div class="cmod-goods-mark">'+
                                                    '<span>${item.location}</span>' +
                                                        '{@if title}'+
                                                        '<span class="cmod-goods-mark-sep">|</span>' +
                                                        '{@/if}' +
                                                        '<span>${item.text}</span>'+
                                                '</div>'+
                                                '{@if item.recommendReason}'+
                                                '<div class="cmod-goods-recommend"><div class="cmod-goods-recommend-content">${item.recommendReason}</div></div>'+
                                                '{@/if}'+
                                                '<div class="cmod-goods-item-title">${item.title}</div>'+
                                                '<div class="cmod-goods-item-tag">'+
                                                    '<div class="cmod-goods-price"><span>${item.price | parsePrice, 0}</span><span class="cm-price-hignlight">${item.price | parsePrice, 1}</span></div>'+
                                                    '<div class="cmod-goods-summary">${item.summary}</div>'+
                                                '</div>'+
                                            '</div>'+
                                        '</div>'+
                                    '{@/each}'+
                                    '</div>'+
                                '</div>'+
                            '{@/if}',
    /**  end **/
};




