import * as Utils from './lib/utils.js';
import * as Tpl from './lib/tpl.js';
import Juicer from './lib/juicer.js';
import $ from './lib/dom.js';
import Lazyload from './lib/lazyload.js';

import './index.scss';

// Polyfill
if (typeof Object.assign != 'function') {
    Object.assign = function (target) {
        'use strict';
        if (target == null) {
            throw new TypeError('Cannot convert undefined or null to object');
        }

        target = Object(target);
        for (let index = 1; index < arguments.length; index++) {
            let source = arguments[index];
            if (source != null) {
                for (let key in source) {
                    if (Object.prototype.hasOwnProperty.call(source, key)) {
                        target[key] = source[key];
                    }
                }
            }
        }
        return target;
    };
}
if (!Array.prototype.includes) {
    Array.prototype.includes = function (searchElement /*, fromIndex*/) {
        'use strict';
        if (this == null) {
            throw new TypeError('Array.prototype.includes called on null or undefined');
        }

        let O = Object(this);
        let len = parseInt(O.length, 10) || 0;
        if (len === 0) {
            return false;
        }
        let n = parseInt(arguments[1], 10) || 0;
        let k;
        if (n >= 0) {
            k = n;
        } else {
            k = len + n;
            if (k < 0) {
                k = 0;
            }
        }
        let currentElement;
        while (k < len) {
            currentElement = O[k];
            if (searchElement === currentElement ||
                (searchElement !== searchElement && currentElement !== currentElement)) { // NaN !== NaN
                return true;
            }
            k++;
        }
        return false;
    };
}

/***
 * https://aone.alibaba-inc.com/project/587037/req/10238004
 * @type {string}
 */
let MTOP_API = 'mtop.trip.walle.position.recommcontent.get';
let ACTION_API = 'mtop.trip.walle.position.action.process';
let BACKUP_IMG = {
    // small: "//img.alicdn.com/tfs/TB1sFigQpXXXXbfXVXXXXXXXXXX-180-120.png",
    small: "//gw.alicdn.com/tfs/TB1IE23SFXXXXXyXVXXXXXXXXXX-117-120.png",
    middle: "//img.alicdn.com/tfs/TB1kCiMQpXXXXXMXXXXXXXXXXXX-226-226.png"
};

// TODO Bridge, MTop
let Bridge, MTop;
try {
    if (window.base) {
        Bridge = window.base.Bridge.Global;
        MTop = window.base.MTop;
    } else {

    }
} catch (e) {
}

const defaultAttr = {
    tplConfig: [],
    params: {},
    getRecommendData: false,
    recommendData: null
};

class CrossMarket {
    /**
     * @param opt.tplConfig         
     * @param opt.params            ,,
     * @param opt.getRecommendData  falsetrue
     */
    constructor(opt) {
        Object.assign(this, defaultAttr, opt);
        Object.assign(this.params, {
            clientSource: CrossMarket._getClientSource(),
            clientPlatform: 'h5'
        });

        if(opt.complete && typeof opt.complete  === "function") {
            this.on("render-complete", (data)=>{
                opt.complete(data);
            });
        }

        if(opt.error && typeof opt.error  === "function") {
            this.on("render-error", (err)=>{
                opt.error(err);
            });
        }
    }

    // 
    static _initGlobalState() {
        this._bindJuicerHandler();
        this._bindEvent();
    }

    // 
    static exhibit(config) {
        let instance = new CrossMarket(config);
        instance.generatePage();

        // 
        var q = (window.goldlog_queue || (window.goldlog_queue = []));
        q.push({
            action: 'goldlog.setMetaInfo',
            arguments: ['aplus-auto-exp', JSON.stringify([{
                logkey: "/tbtrip.crossmarket.marketingModule",
                tag: "div",
                filter: "data-cross-market",
                props: ["data-resource-holder", "data-template-id", "data-trackargs"]
            }])]
        });
    }

    /**
     * 
     */
    generatePage() {
        let config = CrossMarket._dealConfig({
            tplConfig: this.tplConfig,
            params: this.params,
            recommendData: this.recommendData
        });

        if (!this.getRecommendData && config.recommendData && Object.keys(config.recommendData).length) {
            this.dealActiveData(config, config.recommendData);
            return;
        }

        if (!config.params.orderId) return;

        let extraParams = {
            needLogin: true,
            useNative: true,  //nativemtop
            taobao: false,     //nativemtop
            tmall: false,      //nativemtop
            alipay: false,     //nativemtop
            issec: "1"         //10
        };

        let source = '';
        if (Bridge.isTaobao) {
            source = 'mtb';
        } else if (Bridge.isAlipay) {
            source = 'zfb';
        } else if (Bridge.isAlitrip) {
            source = 'trip';
        }

        MTop.request({
            'api': MTOP_API,
            'v': '1.0',
            'params': extraParams,
            'data': {
                resourceHolderNames: '[' + config.resourceHolderNames + ']',
                extraParams: JSON.stringify(config.params || {}),
                issec: "1",  //
                clientPlatform: 'h5',
                source: source
            }
        }).then((resp) => {
            this.dealActiveData(config, resp.data);
        }, (resp) => {
            //
            let showResourceInfo = {num: 0,resource: []};
            this.fire('render-complete', {e: config, showResourceInfo: showResourceInfo});
            this.fire('render-error', resp);
        });
    }

    /**
     *   +  +  + 
     * @param config
     * @param data
     */
    dealActiveData(config, data) {
        let showResourceInfo = {
            num: 0,
            resource: []
        };

        config.tplConfig.forEach((item) => {
            let html = '', tplKey = '';

            item.tpl.forEach((one) => {
                //
                if (!data[one] || !data[one].templateId) return;

                //html
                tplKey = data[one].templateId;
                let tplHtml = Tpl.mods[tplKey] || '';
                html += Juicer(tplHtml, Object.assign({resourceHolderName:one}, data[one]));

                showResourceInfo.resource.push(data[one]);
                showResourceInfo.num++;
            });

            CrossMarket._insertToPage($(item.referenceNode), item.insertType, html);
            let cmodMods = ($(item.referenceNode)[0] && $(item.referenceNode)[0].getElementsByClassName("cmod-module-item")) || [];

            [].slice.call(cmodMods).forEach((node) => {
                CrossMarket._recordGoldExposure(node);
                CrossMarket._exhibitNodes.push(node);
            });

        });

        CrossMarket.checkImageError();
        CrossMarket._lazyLoadImg('.cmod-goods-item img');
        this.fire('render-complete', {e: config, showResourceInfo: showResourceInfo});
    }

    static goToLinK(target) {
        let action = target.getAttribute('data-action');
        let link = target.getAttribute('data-link');
        link = link ? this._absoultePath(link) : "";
        let page = this._absoultePath(target.getAttribute('data-page'));

        let gotoPage = function () {
            // nativenativeH5
            // h5Url
            let isPage = /^(page):\/\/.?/.test(page);

            if (Bridge.isAlitrip && page && isPage) {
                let pageName = page.replace(/\?.*$/i, '').match(/page\:\/\/(.*)/),
                    params = page.replace(/^.*\?/i, '').match(/params\=(.*)$/);

                if (pageName) {
                    pageName = pageName[1];
                }

                if (params) {
                    try {
                        params = JSON.parse(decodeURIComponent(params[1]));
                    } catch (err) {
                        params = null;
                    }
                }

                Bridge.open(pageName, params || {}, function () {
                }, 0, 2);
            } else if (link || page) {
                Bridge.goTo({
                    href: link || page,
                    triggerNode: target
                });
            }
        }

        // actionaction
        if (action) {
            this.handleActionRequest(action, gotoPage);
        } else {
            gotoPage()
        }
    }

    /**
     * 
     * @param param
     * */
    static handleActionRequest(param, fn) {

        let extraParams = {
            useNative: true,  //nativemtop
            taobao: false,     //nativemtop
            tmall: false,      //nativemtop
            alipay: false,     //nativemtop
            issec: "1"         //10
        };

        MTop.request({
            'api': ACTION_API,
            'v': '1.0',
            'params': extraParams,
            'data': {
                actionParam: param,
                issec: "1",  //
                clientPlatform: 'h5'
            }
        }).then((resp) => {
            console.log('');
            fn && fn();
        }, (e) => {
            console.error(e.message);
            fn && fn();
        });
    }

    //
    static _recordGoldExposure(node) {
        // 
        if(node.exposed) return;

        let viewHeight = window.innerHeight;
        let resHolderName = node.getAttribute('data-resource-holder');
        let templateId = node.getAttribute('data-template-id');
        let trackArgs;

        try{
            trackArgs = JSON.parse(node.getAttribute('data-trackargs'));
        } catch (e) {
            console.log(e);
        }

        // 
        if (viewHeight > node.getBoundingClientRect().top) {
            node.exposed  = true;
            if(trackArgs) {
                // 
                // let goKey = `templateId=${templateId}&resourceHoldName=${resHolderName}${this.transfer2Params(trackArgs)}`;
                // window.goldlog && window.goldlog.record('/tbtrip.crossmarket.marketingModule','EXP',goKey,'H1510124625');
            }
        }
    }

    static _insertToPage(referenceNode, insertType, html) {
        if (!referenceNode) return;

        let insertTypeArr = ['append', 'prepend', 'before', 'after'];

        if (insertTypeArr.includes(insertType)) {
            referenceNode[insertType](html);
        }
    }

    // 
    static _lazyLoadImg (selector) {
        var CMLazyLoad = new LazyLoad({
            nodes: selector, //
            delay: 50, //30
            margin: 15, //15
            preciseMode: true, //false
            autoDestroy: false, //true
            useAnim: false, //for
            loadBeforeScroll: false, //For ios6touchstart
            speed: '0.5s', //transition
            autoReload: false, //reload
            type: 'img',  //,img
            attr: 'lazysrc', //
            imgSize: '60',  //tps
            weakSize: '30',  //
            useWebp: true,  //webp
            heightOnly: true, //
            enableDomainMerge: false
        });
    }

    static _dealConfig(config) {
        let resourceHolderNames = [];

        config.tplConfig.forEach((item) => {
            item.tpl.forEach((one) => {
                resourceHolderNames.push('"' + one + '"');
            })
        });

        // OrderId. URL
        for (let key in config.params) {
            if (key == 'orderId' || key == 'returnOrderId') {
                config.params[key] = this._dealOrderId(config.params[key]);
            }
        }

        config.resourceHolderNames = resourceHolderNames.join(',');
        return config;
    }

    static _dealOrderId(orderId) {
        if (/^[0-9]*$/.test(orderId)) return orderId;
        return Utils.getUrlParam(orderId, null, true);
    }

    static checkImageError(data) {
        let images = document.querySelectorAll('.J_crossMarketingImage');
        if (images && images.length) {
            [].slice.call(images).forEach((img) => {
                img.addEventListener('error', () => {
                    let backImg = img.getAttribute('data-backup');
                    if (backImg) {
                        img.setAttribute('src', BACKUP_IMG[backImg]);
                    }
                })
            });
        }
    }

    static _absoultePath(href) {
        if (!href) return '';

        let link = document.createElement('a');
        link.href = href;
        return (link.protocol + '//' + link.host + link.pathname + link.search + link.hash);
    }

    //   1-  2-  3-  4-H5  5-PC
    static _getClientSource() {
        let ua = navigator.userAgent;
        let isAlitrip = /AliTrip/i.test(ua),
            isAlipay = /AlipayClient/i.test(ua),
            isTaobao = /Aliapp\(TB/i.test(ua);

        if (isAlitrip) return "1";
        if (isTaobao) return "2";
        if (isAlipay) return "3";
        return "4";
    }

    static _bindJuicerHandler() {
        Juicer.register("removeProtocol", (src) => {
            return src ? src.replace(/^(http(s)?:\/\/)/g, '//') : "";
        });

        Juicer.register('serialize', (obj) => {
            return obj ? JSON.stringify(obj) : '';
        });

        //string,{[20]}
        Juicer.register("getStrongWorld", (src) => {
            return src.replace(/\{\[(.*)\]\}/g, function ($1, $2) {
                return '<span>' + $2 + '</span>'
            });
        });

        // : Subtitle
        Juicer.register('parseSubTitle', function (content, num) {
            return content.split(/{\s*content\s*}/i)[Number(num)] || "";
        });

        // : price
        Juicer.register('parsePrice', function (price, num) {
            var priceStrs = [];

            if(/(|)/i.test(price)){
                priceStrs.push("");
                priceStrs.push(price.replace("","").replace("",""));
            } else {
                priceStrs.push(price);
            }

            return priceStrs[num] || "";
        });

        // Objecturl params
        Juicer.register('transfer2Params', function (object) {
            return CrossMarket.transfer2Params(object);
        });
    }

    // Objecturl params
    static transfer2Params(object) {
        let key = '';
        try {
            Object.keys(object).forEach(item => {
                key += `&${item}=${encodeURIComponent(object[item])}`;
            });
        } catch (e) {
            console.log(e);
        }
        return key;
    }

    static _bindEvent() {
        // TODO 
        [].slice.call(document.querySelectorAll('body>div')).forEach((item) => {
            item.addEventListener('click', (e) => {
                let target = e.target;
                let links = document.querySelectorAll('.J_crossMarketingLink');

                if (links && links.length) {
                    [].slice.call(links).forEach((l) => {
                        if (target == l || l.contains(target)) {
                            // 
                            let resHolderName = l.getAttribute('data-resource-holder');
                            let templateId = l.getAttribute('data-template-id');
                            let trackArgs;
                            try{
                                trackArgs = JSON.parse(l.getAttribute('data-trackargs'));
                            } catch (e) {
                                console.log(e);
                            }
                            let goKey = `templateId=${templateId}&resourceHoldName=${resHolderName}${this.transfer2Params(trackArgs)}`;
                            window.goldlog && window.goldlog.record('/tbtrip.crossmarket.marketingModule','CLK',goKey,'H1510124625')
                            this.goToLinK(l);
                        }
                    });
                }
            });
        });

        // 
        document.addEventListener('scroll', (e) => {
            CrossMarket._exhibitNodes.forEach((node) => {
                CrossMarket._recordGoldExposure(node);//
            });
        })
    }

    on(event, fn, scope) {
        event = event.toLowerCase();
        fn = fn || (() => {});
        scope = scope || null;
        this.eventList = this.eventList || {};
        this.eventList[event] = this.eventList[event] || [];
        this.eventList[event.toLowerCase()].push({
            fn: fn,
            scope: scope
        });
    }

    fire() {
        if (!this.eventList) {
            return;
        }
        let args = Array.prototype.slice.call(arguments);
        let event = args.shift().toLowerCase();
        let fnList = this.eventList[event] || [];
        fnList.forEach((fnItem) => {
            fnItem['fn'].apply(fnItem['scope'], args);
        })
    }
}

// 
CrossMarket._exhibitNodes = [];
// 
CrossMarket.exposureOffset = 0;

CrossMarket._initGlobalState();
window.CrossMarket = CrossMarket;
export default CrossMarket;



