
/** copy kissy.escapeHtml **/
let escapeReg,
    htmlEntities = {
        '&amp;': '&',
        '&gt;': '>',
        '&lt;': '<',
        '&#x60;': '`',
        '&#x2F;': '/',
        '&quot;': '"',
        '&#x27;': "'"
    },
    reverseEntities = {};

(function () {
    for (let k in htmlEntities) {
        reverseEntities[htmlEntities[k]] = k;
    }
})();

function getEscapeReg() {
    if (escapeReg) {
        return escapeReg;
    }
    let str = '';

    for (let k in htmlEntities){
        str += htmlEntities[k] + '|';
    }
    str = str.slice(0, -1);
    escapeReg = new RegExp(str, 'g');
    return escapeReg;
}

function escapeHtml(str) {
    return (str + '').replace(getEscapeReg(), function (m) {
        return reverseEntities[m];
    });
}
/** copy end **/

export function getUrlParam(name, url, isEscape) {
    url = url || window.location.href;
    let params = parseURL(url, isEscape).params || {};
    return params[name];
}
export function parseURL(url, isEscape) {
    let a = document.createElement('a');
    a.href = url || window.location.href;

    //params
    let _params = a.search.replace(/^\?/, '').split('&'),
        params = {};

    for (let i = 0, len = _params.length; i < len; i++) {
        let key = _params[i].split('=')[0];
        let val = _params[i].split('=')[1] || '';
        if (key) {
            params[key] = isEscape ? escapeHtml(val) : val;
        }
    }

    return {
        origin: a.origin,
        href: a.href,
        host: a.host,
        hostname: a.hostname,
        port: a.port,
        search: a.search,
        params: params,
        protocol: a.protocol.replace(':', ''),
        file: (a.pathname.match(/\/([^\/?#]+)$/i) || [, ''])[1],
        hash: a.hash,
        pathname: a.pathname,//.replace(/^([^\/])/, '/$1')
        relative: (a.href.match(/tps?:\/\/[^\/]+(.+)/) || [, ''])[1],
        segments: a.pathname.replace(/^\//, '').split('/')
    };
}

export function isType(type) {
    return function(obj) {
        return {}.toString.call(obj) == '[object ' + type + ']';
    }
}

export function hasClass(el, name){
    let re = new RegExp("(^|\\s)" + name + "(\\s|$)");
    return re.test(el.className);
}

export function addClass(el, name){

    if(isType('NodeList')(el)){
        el = [].slice.call(el);
    }else if(!isType('Array')(el)){
        el = [el];
    }

    el.forEach((e)=>{
        if(!hasClass(e, name)) {
            let newclass = e.className.split(' ');
            newclass.push(name);
            e.className = newclass.join(' ');
        }
    })
}

export function removeClass(el, name){
    if(isType('NodeList')(el)){
        el = [].slice.call(el);
    }else if(!isType('Array')(el)){
        el = [el];
    }

    el.forEach((e)=>{
        if (hasClass(e, name) ) {
            let re = new RegExp("(^|\\s)" + name + "(\\s|$)", 'g');
            e.className = e.className.replace(re, ' ');
        }
    })
}



